.Sidebar{
    display: flex;
    flex-direction: column;
    gap: 40px;
}

.sidebar img{
width:60px;
cursor:pointer;
}
.sidebar-list{
    display: flex;
    flex-direction: column;
    gap: 20px;
    align-items: center;
   height:0px;
    transition: 300ms;
}
.sidebar-list-item{
    border-radius: 50%;
    height: 26px;
    width: 26px;
    list-style:none ;
}
.sidebar-list-active{
    height: 300px;
}