.note{
    padding: 25px;
    height: 280px;
    width:260px;
    display: flex;
    flex-direction: column;
    background-color: blueviolet;
    border-radius: 30px;
}
.note_text{
    flex: 1;
    resize: none;
    background-color: transparent;
    font-size: 1rem;
    outline: none;
    line-height: 1.875rem;
    border: none;
}
.sty{
   float: right;
   cursor: pointer;
}
notes-footer{
    display: flex;
    justify-content: space-between;
    align-items: center;
}